import {followedApi, getDataUsers} from "../api/api";
import {updateObjectInArray} from "../auxiliary/updateObject";
import {PhotosType} from "../Type";

const onFollowAC = 'on_follow';
const onNotFollowAC = 'on_notFollow';
const onSetUsersAC = 'on_SetUsers';
const setCurrentPageAC = 'set_Current_Page ';
const getTotalUsersCountAC = 'get_Total_Users_Count ';
const toggleIsFetchingAC = 'toggle_Is_Fetching';
const followedButtonStatusAC = 'followed_Button_Status';

type UserType = {
    id: number,
    name: string,
    status: string,
    photos: PhotosType,
    followed: boolean
};

export type InitialUsersStateType = {
    users: UserType,
    followed: boolean,
    pageSize: number,
    totalUsers: number,
    currentPage: number,
    isFetching: boolean,
    followedStatus: any
};

let initialUsersState: InitialUsersStateType = {
    users: [] as any,
    followed: false,
    pageSize: 30,
    totalUsers: 0,
    currentPage: 1,
    isFetching: true,
    followedStatus: [] as Array<number>
};

const usersReducer = (state = initialUsersState, action: any) => {
    switch (action.type) {
        case onFollowAC:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, 'id', {followed: true})
            };
        case onNotFollowAC:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, 'id', {followed: false})
            };
        case onSetUsersAC:
            return {
                ...state, users: action.users
            };
        case setCurrentPageAC:
            return {
                ...state, currentPage: action.currentPage
            };
        case getTotalUsersCountAC:
            return {
                ...state, totalUsers: action.totalCount
            };
        case toggleIsFetchingAC:
            return {
                ...state, isFetching: action.isFetching
            };
        case followedButtonStatusAC: {
            return {
                ...state,
                followedStatus: action.statusFollowed
                    ? [...state.followedStatus, action.userId]
                    : state.followedStatus.filter((id: any) => id !== action.userId)
            }
        }
        default:
            return state;
    }
};

export const yesFollow = (userId: number) => ({type: onFollowAC, userId});
export const notFollow = (userId:number) => ({type: onNotFollowAC, userId});
export const setUsers = (users: UserType) => ({type: onSetUsersAC, users});
export const setCurrentPage = (currentPage: number) => ({type: setCurrentPageAC, currentPage});
export const getTotalUsersCount = (totalCount: number) => ({type: getTotalUsersCountAC, totalCount});
export const toggleIsFetching = (isFetching: boolean) => ({type: toggleIsFetchingAC, isFetching});
export const fixedFollowedButtonStatus = (statusFollowed: boolean, id:number) => ({
    type: followedButtonStatusAC, statusFollowed, userId: id
});

const followNotFollowBox = async (dispatch: any, userId: number, apiMethod: any, actionStatus: Function) => {
    dispatch(fixedFollowedButtonStatus(true, userId));
    let data = await apiMethod(userId);
    if (data.resultCode === 0) {
        dispatch(actionStatus(userId))
    }
    dispatch(fixedFollowedButtonStatus(false, userId));
};

export const getUsersThunk = (currentPage: number, pageSize: number) => async (dispatch: any) => {
    dispatch(toggleIsFetching(true));
    let data = await getDataUsers.getUsers(currentPage, pageSize);

    dispatch(setCurrentPage(currentPage));
    dispatch(toggleIsFetching(false));
    dispatch(setUsers(data.items));
    dispatch(getTotalUsersCount(data.totalCount));
};

export const notFollowButtonStatusThunk = (userId: number) => async (dispatch: any) => {
    await followNotFollowBox(dispatch, userId, followedApi.notFollowedUsers, notFollow)
};

export const followButtonStatusThunk = (userId: number) => async (dispatch: any) => {
    await followNotFollowBox(dispatch, userId, followedApi.yesFollowedUsers, yesFollow)
};

export default usersReducer;


