import {ProfileAPI} from "../api/api";
import {stopSubmit} from "redux-form";
import {PhotosType, PostsType, ProfileType} from "../Type";

const UPDATE_NEW_TEXT = 'update_New_TextAC';
const SET_USER_PROFILE = 'set_User_ProfileAC';
const SET_STATUS = 'set_StatusAC';
const DELETE_POST = 'delete_PostAC';
const SAVE_PHOTO_USER = 'save_Photo_UserAC';

type InitialProfileStateType = {
    posts: Array<PostsType>
    profile: ProfileType | null,
    status: '',
};

const initialProfileState: InitialProfileStateType = {
    posts: [
        {id: 0, message: 'Hello!', like: 10},
        {id: 1, message: 'How are you.', like: 8},
    ] as Array<PostsType>,
    profile: null,
    status: '',
};

const ProfileReducer = (state = initialProfileState, action: any) => {
    switch (action.type) {
        case UPDATE_NEW_TEXT: {
            const postsLength = state.posts.length;
            for (let i = state.posts.length; i > state.posts.length; i++) {
            }
            return {
                ...state,
                posts: [...state.posts, {
                    id: postsLength,
                    message: action.newText,
                    like: 0
                }]
            }
        }
        case DELETE_POST: {
            return {...state, posts: state.posts.filter(p => p.id !== action.postId)}
        }
        case SET_USER_PROFILE: {
            return {...state, profile: action.profile}
        }
        case SET_STATUS: {
            return {...state, status: action.status}
        }
        case SAVE_PHOTO_USER: {
            return {...state, profile: {...state.profile, photos: action.photos}}
        }
        default:
            return state;
    }
};

type UpdateNewTextActionType = {
    type: typeof UPDATE_NEW_TEXT,
    newText: string
}
type SetUserProfileActionType = {
    type: typeof SET_USER_PROFILE,
    profile: ProfileType
}
type SetStatusActionType = {
    type: typeof SET_STATUS,
    status: string
}
type DeletePostActionType = {
    type: typeof DELETE_POST,
    postId: number
}
type SetSavePhotoUserActionType = {
    type: typeof SAVE_PHOTO_USER,
    photos: PhotosType
}

export const updateNewTextAction = (text: string): UpdateNewTextActionType => ({type: UPDATE_NEW_TEXT, newText: text});
export const setUserProfileAction = (profile: ProfileType): SetUserProfileActionType => ({
    type: SET_USER_PROFILE,
    profile
});
export const setStatusAction = (data: string): SetStatusActionType => ({type: SET_STATUS, status: data});
export const deletePostAction = (postId: number): DeletePostActionType => ({type: DELETE_POST, postId});
export const setSavePhotoUserAction = (photos: PhotosType): SetSavePhotoUserActionType => ({
    type: SAVE_PHOTO_USER,
    photos
});

export const showUserProfileThunk = (userId: number) => async (dispatch: any) => {
    const data = await ProfileAPI.userProfile(userId);
    dispatch(setUserProfileAction(data));
};

export const getStatusThunk = (userId: number) => async (dispatch: any) => {
    const data = await ProfileAPI.getStatusUser(userId);
    dispatch(setStatusAction(data))
};

export const updateStatusThunk = (status: string) => async (dispatch: any) => {
    const response = await ProfileAPI.updateStatus(status);
    if (response.data.resultCode === 0) {
        dispatch(setStatusAction(status))
    }
};

export const savePhotoUserThunk = (photo: PhotosType) => async (dispatch: any) => {
    const response = await ProfileAPI.updatePhotoUser(photo);
    if (response.data.resultCode === 0) {
        dispatch(setSavePhotoUserAction(response.data.data.photos))
    }
};
export const saveInfoAboutUserThunk = (profile: ProfileType) => async (dispatch: any, getState: any) => {
    const userId = getState().authentic.userId;
    const response = await ProfileAPI.updateInfoAboutUser(profile);
    if (response.data.resultCode === 0) {
        dispatch(showUserProfileThunk(userId));

    } else {
        dispatch(stopSubmit('InfoAboutUser', {_error: response.data.messages}))
    }
};

export default ProfileReducer;

