import {identificationMeThunk} from "./AuthenticReducer";

const initializedAC = 'initializedAC';

export type InitializedType = {
    initialized: boolean
}

let initialAppState: InitializedType = {
    initialized: false
};

const AppReducer = (state = initialAppState, action: any): InitializedType => {
    switch (action.type) {
        case initializedAC:
            return {
                ...state,
                initialized: true
            };
        default:
            return state;
    }
};

type InitializedACType = {
    type: typeof initializedAC
}
export const setInitialized = (): InitializedACType => ({type: initializedAC});

export const initializeThunk = () => (dispatch: any) => {
    let promise = dispatch(identificationMeThunk());
    Promise.all([promise])
        .then(() => {
            dispatch(setInitialized())
        })
};

export default AppReducer;