import {authenticAPI, securityApi} from "../api/api";
import {stopSubmit} from "redux-form";

const setUserDataAC = 'setUserDataAC';
const setCaptchaUrlAC = 'setCaptchaUrlAC';

export type InitialAuthenticStateType = {
    userId: number | null,
    email: string | null,
    login: string | null,
    isAuth: boolean,
    captchaUrl?: string | null
}

let initialAuthenticState: InitialAuthenticStateType = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    captchaUrl: null
};

const AuthenticReducer = (state = initialAuthenticState, action: any): InitialAuthenticStateType => {
    switch (action.type) {
        case setUserDataAC:
        case setCaptchaUrlAC:
            return {
                ...state, ...action.newData
            };
        default:
            return state;
    }
};

type SetUserDataType = {
    type: typeof setUserDataAC,
    newData: InitialAuthenticStateType
}

export const setUserData = (userId: number | null, email: string | null, login: string | null, isAuth: boolean):
    SetUserDataType => ({
    type: setUserDataAC,
    newData: {userId, email, login, isAuth}
});

type SetCaptchaUrlType = {
    type: typeof setCaptchaUrlAC,
    newData: { captchaUrl: string }
}

export const setCaptchaUrl = (captchaUrl: string): SetCaptchaUrlType => ({
    type: setCaptchaUrlAC,
    newData: {captchaUrl}
});

export const identificationMeThunk = () => async (dispatch: any) => {
    let response = await authenticAPI.me();
    if (response.data.resultCode === 0) {
        let {id, email, login} = response.data.data;
        dispatch(setUserData(id, email, login, true))
    }
};

export const loginThunk = (email: string, password: number, rememberMe: boolean, captcha: string) =>
    async (dispatch: any) => {
        let response = await authenticAPI.login(email, password, rememberMe, captcha);
        if (response.data.resultCode === 0) {
            dispatch(identificationMeThunk())
        } else {
            if (response.data.resultCode === 10) {
                dispatch(getCaptchaUrlThunk())
            }
            let message = (response.data.messages.length > 0 ? response.data.messages[0] : "Some error!");
            dispatch(stopSubmit('login', {_error: message}))
        }
    };

export const logoutThunk = () => async (dispatch: any) => {
    let response = await authenticAPI.logout()
    if (response.data.resultCode === 0) {
        dispatch(setUserData(null, null, null, false))
    }
};
