//     }
// };

export const getCaptchaUrlThunk = () => async (dispatch: any) => {
    const response = await securityApi.getCaptchaUrl();
    const captchaUrl = response.data.url;
    dispatch(setCaptchaUrl(captchaUrl));
};

export default AuthenticReducer;